import {Component} from '@angular/core';
import {FormController, FormControllerType, MenuItem} from '../../../shared/modules';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'app-add-dynamic-form',
    templateUrl: './add-dynamic-form.component.html',
    styleUrls: ['./add-dynamic-form.component.scss']
})
export class AddDynamicFormComponent {
    public formControls: FormController[] = [];
    public formFields = [];
    settings = {
        hideSubHeader: true,
        attr: {
            class: 'table table-bordered'
        },
        actions: {
            add: false,
            edit: false,
            delete: false,
        },
        columns: {
            classes: {
                title: 'Classes',
            },
            value: {
                title: 'Value',
            },
            types: {
                title: 'Types',
            },
            min: {
                title: 'Min',
            },
            max: {
                title: 'Max',
            },
            label: {
                title: 'Label',
            },
            name: {
                title: 'Name',
            },
            type: {
                title: 'Type',
            },
            data: {
                title: 'Data',
            }
        },
    };
    private formControllerClass = 'col-lg-3 col-md-3 col-sm-12 col-xs-12 mb-3';

    /*
        public label: string;
        public classes: string;
        public value: any;
        public required: boolean;
        public name: string;
        public min: any;
        public max: any;
        public data: MenuItem[];
    public types: string[];
        public type: FormControllerType;
    */

    public labelController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: 3,
        max: 50,
        label: 'Label',
        name: 'label',
        type: FormControllerType.text,
        data: null
    });
    public desktopWidthController = new FormController({
        classes: this.formControllerClass,
        value: 'col-md-4',
        types: null,
        min: null,
        max: null,
        label: 'Desktop Width',
        name: 'desktopWidth',
        type: FormControllerType.select,
        data: this.getWidthData('md')
    });
    public tabWidthController = new FormController({
        classes: this.formControllerClass,
        value: 'col-sm-4',
        types: null,
        min: null,
        max: null,
        label: 'Tab Width',
        name: 'tabWidth',
        type: FormControllerType.select,
        data: this.getWidthData('sm')
    });
    public mobileWidthController = new FormController({
        classes: this.formControllerClass,
        value: 'col-xs-12',
        types: null,
        min: null,
        max: null,
        label: 'Mobile Width',
        name: 'mobileWidth',
        type: FormControllerType.select,
        data: this.getWidthData('xs')
    });
    public requiredController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: null,
        max: null,
        label: 'Is Mandatory',
        name: 'required',
        type: FormControllerType.radio,
        data: [new MenuItem({id: '1', label: 'Yes', isSelected: false}), new MenuItem({id: '0', label: 'No', isSelected: false})]
    });
    public nameController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: 3,
        max: 50,
        label: 'Name',
        name: 'name',
        type: FormControllerType.text,
        data: null
    });
    public minValueController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: 5,
        max: 50,
        label: 'Min Value',
        name: 'minValue',
        type: FormControllerType.number,
        data: null
    });
    public maxValueController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: 5,
        max: 50,
        label: 'Max Value',
        name: 'maxValue',
        type: FormControllerType.number,
        data: null
    });
    public minDateController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: null,
        max: null,
        label: 'Min Date',
        name: 'minDate',
        type: FormControllerType.date,
        data: null
    });
    public maxDateController = new FormController({
        classes: this.formControllerClass,
        value: null,
        types: null,
        min: null,
        max: null,
        label: 'Max Date',
        name: 'maxDate',
        type: FormControllerType.date,
        data: null
    });
    public dataController = new FormController({
        classes: this.formControllerClass,
        value: [],
        types: null,
        min: 3,
        max: 5,
        label: 'Data',
        name: 'data',
        type: FormControllerType.tags,
        data: null
    });
    public typesController = new FormController({
        classes: this.formControllerClass,
        value: 'text',
        types: null,
        min: null,
        max: null,
        label: 'Type',
        name: 'type',
        type: FormControllerType.select,
        data: this.getTypeData()
    });

    constructor(public activeModal: NgbActiveModal) {
        this.formControls = [
            this.labelController,
            this.typesController,
            this.nameController,
            this.requiredController,
            this.minValueController,
            this.maxValueController,
            this.minDateController,
            this.maxDateController,
            this.desktopWidthController,
            this.tabWidthController,
            this.mobileWidthController,
            this.dataController
        ];

        this.minValueController.hide = false;
        this.maxValueController.hide = false;
        this.minDateController.hide = true;
        this.maxDateController.hide = true;
        this.dataController.hide = true;

        this.labelController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.typesController.onChange = newController => {
            console.log(newController.value);
            if (
                newController.value === FormControllerType.select ||
                newController.value === FormControllerType.checkbox ||
                newController.value === FormControllerType.radio
            ) {
                this.minValueController.hide = true;
                this.maxValueController.hide = true;
                this.minDateController.hide = true;
                this.maxDateController.hide = true;
                this.dataController.hide = false;
            } else if (
                newController.value === FormControllerType.date ||
                newController.value === FormControllerType.datetime ||
                newController.value === FormControllerType.month
            ) {
                this.minValueController.hide = true;
                this.maxValueController.hide = true;
                this.minDateController.hide = false;
                this.minDateController.type = newController.value;
                this.maxDateController.hide = false;
                this.maxDateController.type = newController.value;
                this.dataController.hide = true;
            } else {
                this.minDateController.hide = true;
                this.maxDateController.hide = true;
                this.minValueController.hide = false;
                this.maxValueController.hide = false;
                this.dataController.hide = true;
            }
        };
        this.requiredController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.minValueController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.maxValueController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.minDateController.onChange = formControl => {
            console.log(formControl.value);
            this.maxDateController.min = this.minDateController.value;
        };
        this.maxDateController.onChange = formControl => {
            console.log(formControl.value);
            this.minDateController.max = this.maxDateController.value;
        };
        this.desktopWidthController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.tabWidthController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.mobileWidthController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.dataController.onChange = formControl => {
            console.log(formControl.value);
        };
        this.nameController.onChange = formControl => {
            formControl.value = formControl.value.replace(/[^a-zA-Z]/g, '');
            let splitString: string[];
            splitString = formControl.value.split('');
            splitString[0] = splitString[0].toLowerCase();
            formControl.value = splitString.join('');
        };
        this.onOptionChange(this.formControls);
    }

    getWidthData(size: string): MenuItem[] {
        return [
            new MenuItem({id: 'col-' + size + '-1', label: 'Size 01', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-2', label: 'Size 02', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-3', label: 'Size 03', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-4', label: 'Size 04', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-5', label: 'Size 05', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-6', label: 'Size 06', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-7', label: 'Size 07', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-8', label: 'Size 08', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-9', label: 'Size 09', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-10', label: 'Size 10', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-11', label: 'Size 11', isSelected: false}),
            new MenuItem({id: 'col-' + size + '-12', label: 'Size 12', isSelected: false}),
        ];
    }

    closeModel(crossClick: string) {
        this.activeModal.dismiss(crossClick);
    }

    private getTypeData(): MenuItem[] {
        return [
            new MenuItem({id: 'text', label: 'Text Input', isSelected: false}),
            new MenuItem({id: 'checkbox', label: 'Check Box', isSelected: false}),
            new MenuItem({id: 'date', label: 'Date', isSelected: false}),
            new MenuItem({id: 'datetime-local', label: 'Date and Time', isSelected: false}),
            new MenuItem({id: 'email', label: 'Email', isSelected: false}),
            new MenuItem({id: 'file', label: 'File', isSelected: false}),
            new MenuItem({id: 'month', label: 'Month', isSelected: false}),
            new MenuItem({id: 'number', label: 'Number', isSelected: false}),
            new MenuItem({id: 'select', label: 'Drop Down', isSelected: false}),
            new MenuItem({id: 'radio', label: 'Radio', isSelected: false}),
            new MenuItem({id: 'password', label: 'Password', isSelected: false}),
        ];
    }

    onOptionChange($event: any) {
    }

    onFormSave($event: FormController[]) {
        debugger;
        this.formFields.push(this.getNewDynamicFormController());
    }

    getNewDynamicFormController(): FormController {
        let newController: FormController;
        if (
            this.typesController.value === FormControllerType.select ||
            this.typesController.value === FormControllerType.checkbox ||
            this.typesController.value === FormControllerType.radio
        ) {
            newController = new FormController(
                {
                    classes: this.mobileWidthController.value + ' ' + this.desktopWidthController.value + ' ' + this.tabWidthController.value,
                    value: null,
                    types: null,
                    required: this.requiredController.value ? true : false,
                    name: this.nameController.value,
                    max: null,
                    min: null,
                    label: this.labelController.value,
                    type: this.typesController.value,
                    data: this.dataController.value
                }
            );
        } else if (
            this.typesController.value === FormControllerType.date ||
            this.typesController.value === FormControllerType.datetime ||
            this.typesController.value === FormControllerType.month
        ) {
            newController = new FormController(
                {
                    classes: this.mobileWidthController.value + ' ' + this.desktopWidthController.value + ' ' + this.tabWidthController.value,
                    value: null,
                    types: null,
                    required: this.requiredController.value ? true : false,
                    name: this.nameController.value,
                    max: this.maxDateController.value,
                    min: this.minDateController.value,
                    label: this.labelController.value,
                    type: this.typesController.value,
                    data: [],
                }
            );
        } else {
            newController = new FormController(
                {
                    classes: this.mobileWidthController.value + ' ' + this.desktopWidthController.value + ' ' + this.tabWidthController.value,
                    value: null,
                    types: null,
                    required: this.requiredController.value ? true : false,
                    name: this.nameController.value,
                    max: this.maxValueController.value,
                    min: this.minValueController.value,
                    label: this.labelController.value,
                    type: this.typesController.value,
                    data: [],
                }
            );
        }
        return newController;
    }
}
